<template>
    <v-container class="pa-0 ma-0">
        <v-card class="cijfers pa-0 ma-0" color="#ffffff" height="40vh">
            <v-card-title class="black--text headline justify-center">
              
                <button @click="getGradesAtStatup()">  Cijfers</button>
            </v-card-title>
                <v-list light class="ma-3" v-if="this.isLoading">
                    <v-list-tile
                        v-for="test in this.lastFiveTests"
                        :key="test.id">
                        <v-list-tile-content>
                            <v-list-tile-title v-text="test.name"> </v-list-tile-title>                            
                        </v-list-tile-content>
                        <v-list-tile-avatar v-text="test.grade"></v-list-tile-avatar>
                    </v-list-tile>
                </v-list>
                <v-spacer></v-spacer>
        </v-card>
    </v-container>
</template>
<script>
import { mapActions, mapGetters, mapState } from 'vuex';
export default {
    data () {
        return {
            tests: [],
            lastFiveTests: [],
            amountOfItemsOnDisplay: 7
        }
    },

    computed: {
        ...mapState('auth', [
            'auth'
        ]),
        ...mapState('user', [
            'user'
        ]),
        ...mapState('grades', [
            'grades'
        ]),
        ...mapGetters('user', [
            'getUser'
        ]),
        ...mapGetters('grades', [
            'getGrades',
            'isLoading',
            'gradesExist'
        ]),
    },

    methods: {
        getGradesFromStore: function() {
            this.loadGrades(this.getUser.id, this.auth)
        },
        ...mapActions('grades', [
            'loadGrades'
        ])
    },

    watch: {
        grades() {
            this.lastFiveTests = [];
            if(this.grades.length < this.amountOfItemsOnDisplay){
                this.amountOfItemsOnDisplay = this.grades.length;
            }
            for(var i = 1 ;i <= this.amountOfItemsOnDisplay; i++) {
                this.lastFiveTests.push(this.grades[this.grades.length - i])
            }
        }
    },
    beforeMount() {
        this.getGradesFromStore();
    },
}
</script>
<style lang="scss" scoped>

    .cijfers {
        min-height: fit-content;
    }
</style>

