<template>
  <div class="pa-0">
    <v-btn
      v-if="showSubscribeButton"
      color="primary black--text"
      href="courses/register"
    >Inschrijven</v-btn>
    <template v-if="showCards">
      <v-layout row wrap>
        <v-flex v-for="course of courses" :key="course.course_code" xs12 sm3 class="pa-2">
          <v-card>
            <v-card-title primary-title>
              <div>
                <div class="headline" v-text="course.course_code"></div>
                <span v-text="course.course_description"></span>
              </div>
            </v-card-title>
            <!-- <v-card-text v-text="course.course_description"></v-card-text> -->
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn fab small icon>
                <v-icon @click="setAndToggleDialog(course)" medium color="red">delete</v-icon>
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </template>

    <template v-if="!showCards">
      <v-card color="#FFF">
        <v-card-title class="black--text headline justify-center">Vakken</v-card-title>
        <v-list light class="ma-0 pa-3" >
          <v-list-tile v-for="course in dashboardCoursesToShow" :key="course.course_code">
            <v-list-tile-content>
              <v-list-tile-title v-text="course.course_code"></v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-avatar v-text="course.course_description"></v-list-tile-avatar>
          </v-list-tile>
        </v-list>
      </v-card>
    </template>

    <confirmation-box
      :showDialog="dialog"
      :title="dialogTitle"
      buttonText="Uitschrijven"
      :loadingText="dialogLoadingText"
      :method="unsubsribeFromCourse"
      v-on:toggle-dialog="dialog = false"
    />

    <v-alert v-model="alert" dismissible :type="alertType">{{alertMsg}}</v-alert>
  </div>
</template>

<script>
import axios from "axios";
import Confirmation from "../Dialogs/Confirmation.vue";
import { mapGetters } from "vuex";

export default {
  name: "courses",
  props: {
    showSubscribeButton: {
      default: true,
      type: Boolean
    },
    showCards: {
      default: true,
      type: Boolean
    }
  },
  methods: {
    unsubsribeFromCourse() {
      let self = this;
      return axios
        .delete(
          "api/courses/" +
            this.selectedCourseId +
            "/users/" +
            this.getUser.id +
            "/unregister"
        )
        .then(function(response) {
          self.toggleAlert("success");
          var index = self.courses
            .map(function(e) {
              return e.course_code;
            })
            .indexOf(response.data.course_code);
          self.courses.splice(index, 1);
        })
        .catch(function(error) {
          self.toggleAlert("error");
        });
    },
    toggle() {
      this.dialog = !this.dialog;
    },
    setCurrentCourse(course) {
      this.selectedCourse = course.course_code;
      this.selectedCourseId = course.id;
    },
    setDialogInfo() {
      this.dialogTitle =
        "Wilt u zich uitschrijven voor het vak " + this.selectedCourse + "?";
      this.dialogLoadingText =
        "Uitschrijven voor het vak " + this.selectedCourse + "...";
    },
    setAndToggleDialog(course) {
      this.setCurrentCourse(course);
      this.setDialogInfo();
      this.toggle();
    },
    toggleAlert(alertType) {
      this.alert = !this.alert;
      if (alertType == "success") {
        this.alertMsg =
          "U bent uitgeschreven voor het vak " + this.selectedCourse + ".";
      } else {
        this.alertMsg = "Er is iets fout gegaan tijdens het uitschrijven.";
      }
      this.alertType = alertType;
    }
  },
  computed: {
    ...mapGetters("user", ["getUser"])
  },
  data: () => ({
    courses: null,
    dialog: false,
    selectedCourse: "",
    selectedCourseId: 0,
    dialogTitle: "",
    dialogLoadingText: "",
    alert: false,
    alertMsg: "",
    alertType: "success",
    dashboardCoursesToShow:[],
    amountOfItemsOnDisplay: 6
  }),
  created() {
    axios
      .get("api/courses/users/" + this.getUser.id)
      .then(response => (this.courses = response.data));   
  },
  watch: {
    courses:{
      handler: function() {
        for(var i = 1 ;i <= this.amountOfItemsOnDisplay; i++) {
          this.dashboardCoursesToShow.push(this.courses[this.courses.length - i])
        }
        console.log(this.dashboardCoursesToShow)
      }
    }
  },
  components: {
    "confirmation-box": Confirmation
  }
};
</script>

