<template >
    <v-container class="pa-0 ma-0">
        <v-card class="testing pa-0 ma-0" height="40vh" color="#FFFFFF">
            <v-card-title class="black--text headline justify-center">Voortgang</v-card-title>
        
            <v-card-text>
                <div class="progress_medium">
                    <div class="toprow">
                        <!-- 100% / 60 EC = 1.67  -->
                        <v-progress-circular
                            :rotate="-90"
                            :size="120"
                            :width="20"
                            :value="amountOfECFirstYear * 1.67"
                            color="success"
                            class="toprow"
                        >
                        {{amountOfECFirstYear}}
                    </v-progress-circular>
                    <p class="black--text text-xs-center">Eerste jaar</p>
                    </div>
                    <div class="toprow">
                        <v-progress-circular
                            :rotate="-90"
                            :size="120"
                            :width="20"
                            :value="amountOfECSecondYear * 1.67"
                            color="success"
                            class="toprow"
                        >
                        {{amountOfECSecondYear}}
                        </v-progress-circular>
                        <p class="black--text text-xs-center">Tweede jaar</p>
                    </div>
                </div>
                <div width="fit-content" class="progress_medium">
                    <div class="bottom-row" width="40%">
                        <v-progress-circular
                            :rotate="-90"
                            :size="70"
                            :width="11"
                            :value="amountOfECStage * 1.67 * 2" 
                            color="success"
                            class="bottom-row"
                        >
                        {{amountOfECStage}}
                        </v-progress-circular>
                        <p class="black--text text-xs-center">Stage</p>
                    </div>
                    <div class="bottom-row" width="40%">
                        <v-progress-circular
                            :rotate="-90"
                            :size="70"
                            :width="11"
                            :value="amountOfECHoofdfase * 1.67 * 2" 
                            color="success"
                            class="bottom-row"
                        >
                        {{amountOfECHoofdfase}}
                        </v-progress-circular>
                        <p class="black--text text-xs-center">Hoofdfase</p>
                    </div>
                    <div class="bottom-row" width="40%">
                        <v-progress-circular
                            :rotate="-90"
                            :size="70"
                            :width="11"
                            :value="amountOfECMinor * 1.67 * 2"
                            color="success"
                            class="bottom-row"
                        >
                        {{amountOfECMinor}}
                        </v-progress-circular>
                        <p class="black--text text-xs-center">Minor</p>
                    </div>
                    <div class="bottom-row" width="40%">
                        <v-progress-circular
                            :rotate="-90"
                            :size="70"
                            :width="11"
                            :value="amountOfECAfstudeerstage * 1.67 * 2"
                            color="success"
                            class="bottom-row"
                        >
                        {{amountOfECAfstudeerstage}}
                        </v-progress-circular>
                        <p class="black--text text-xs-center progress_medium">Afstudeerstage</p>
                    </div>
                </div>
            </v-card-text>
        </v-card>
    </v-container>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex';
export default {
    data () {
        return {
            amountOfECFirstYear: 0,
            amountOfECSecondYear: 0,
            amountOfECStage: 0,
            amountOfECMinor: 0,
            amountOfECHoofdfase: 0,
            amountOfECAfstudeerstage: 0
        }
    },
    computed: {
        ...mapState('auth', [
            'auth'
        ]),
        ...mapState('user',[
            'user'
        ]),
        ...mapState('grades', [
            'grades',
            'progress'
        ]),
        ...mapGetters('user',[
            'getUser'
        ]),
        ...mapGetters('grades', [
            'getGrades',
            'getProgress'
        ])
    },
    methods: {
        getProgressAtStartup: function() {
            this.loadGrades(this.user.id, this.auth)
            this.loadProgress(this.user.id, this.auth)
            // this.grades = this.getGrades;
        },
        ...mapActions('grades', [
            'loadGrades',
            'loadProgress'
        ]),
        courseIsDone: function(courseName) {
            var found = this.grades.find(grade => grade.name === courseName);
            if(found && found.grade > 5.4){
                return 30
            }
            return 0
        }
    },
    watch: {
        grades() {
                this.amountOfECMinor = this.courseIsDone('MINOR');
                this.amountOfECStage = this.courseIsDone('STAGE');
                this.amountOfECAfstudeerstage = this.courseIsDone('AF-STAGE');
        },
        progress() {
                var items = []
                for (let item of Object.values(this.progress)) {
                    items.push(item)
                }
                this.amountOfECFirstYear = items[0] || 0;
                this.amountOfECSecondYear = items[1] || 0;
                this.amountOfECHoofdfase = (items[2]  - this.amountOfECMinor - this.amountOfECStage - this.amountOfECAfstudeerstage) || 0
        }
    },
    mounted() {
        this.getProgressAtStartup()
    }


}

</script>

