import Vue from 'vue'
import Router from 'vue-router'
import store from './store'

//Component imports
import Login from './components/Login.vue'
import Dashboard from './components/Dashboard.vue'
import StudentRegister from './components/StudentRegister.vue'
import TeacherRegister from './components/TeacherRegister.vue'
// import Register from './components/Register.vue'
import Courses from './components/Courses/Courses.vue'
import RegisterCourse from './components/Courses/RegisterCourse.vue'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/Login',
      name: 'redictLogin',
      redirect: '/'
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: Dashboard,
    },
    {
      path: '/register_student',
      name: 'registerStudent',
      component: StudentRegister,
    },
    {
      path: '/register_teacher',
      name: 'registerTeacher',
      component: TeacherRegister,
    },
    {
      path: '/courses',
      name: 'courses',
      component: Courses
    },
    {
      // TO-DO: make this children of the /courses route
      path: '/courses/register',
      name: 'register course',
      component: RegisterCourse
    }
  ]
})

router.beforeEach((to, from, next) => {
  if(store.getters['auth/loggedin']) { 

    if (['Login', 'redictLogin'].indexOf(to.name) === -1) { 
      next();
    } else { 
      next({ name: 'dashboard'})
    }

  } else { 
    if (['Login', 'redictLogin'].indexOf(to.name) === -1) {
      next('/')
    } else {
      next();
    }
  }
});

export default router